<template>
  <div class="hs-container">
    <c-header :title="$t('base.MyIntergral')"></c-header>
    <hs-scroll class="table-view">
      <ul>
        <hs-section>
          <hs-cell-1 :showLogo="false">
            <div class="current-integral">
              <div class="left">
                <p>{{$t('base.CurrentIntergral')}}</p>
                <div class="score">
                  <p>200000</p>
                  <p>{{$t('base.Intergral')}}</p>
                </div>
              </div>
              <div class="right">
                <div class="content">
                  <div class="logo" style="background-image: url('static/image/integral/explain.png')"></div>
                  <p>{{$t('base.IntergralDescription')}}</p>
                </div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
        <hs-section>
          <hs-cell-1 :showLogo="false" v-for="n in 15" :key="n">
            <div class="integral-item">
              <div class="left">
                <p>{{$t('base.OrderIntergral')}}</p>
                <p>2018-08-20</p>
              </div>
              <div class="right">
                <p>+10</p>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
      </ul>
    </hs-scroll>
  </div>
</template>
<script type="text/ecmascript-6">
// 积分页面
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
export default {
  components: {
    CHeader,
    HsScroll,
    HsSection,
    HsCell1
  },
  methods: {
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  .table-view
    width 100%
    height calc(100% - 45px)
    overflow hidden
    .current-integral
      @extend .flex-row
      @extend .flex-start
      @extend .flex-top
      width 100%
      padding 15px 5px
      >.left
        @extend .flex-column
        @extend .flex-center
        @extend .flex-top
        width calc(100% - 90px)
        padding 20px 0
        font-size 0.9rem
        >.score
          @extend .flex-row
          @extend .flex-start
          @extend .flex-bottom
          margin-top 8px
          >p
            &:nth-child(1)
              font-size 2rem
              color $color_theme
            &:nth-child(2)
              margin-bottom 5px
              margin-left 5px
      >.right
        @extend .flex-row
        @extend .flex-end
        @extend .flex-top
        width 90px
        font-size 0.9rem
        >.content
          @extend .flex-row
          @extend .flex-end
          width 100%
          height 16px
          color $color_gray_text
          >.logo
            width 16px
            height 16px
            background-size 100% 100%
            margin-right 5px
    .integral-item
      @extend .flex-row
      @extend .flex-between
      width 100%
      padding 10px 5px
      >.left
        @extend .flex-column
        @extend .flex-top
        width 70%
        >p
          &:nth-child(1)
            font-size 1rem
            font-weight bold
          &:nth-child(2)
            font-size 0.8rem
            color $color_gray_text
            margin-top 5px
      >.right
        @extend .flex-row
        @extend .flex-end
        width 30%
        color $color_theme
        font-size 1rem
</style>



